import React, { Component } from 'react';
import {
  Input,
  Form,
  message,
  Popconfirm,
  Checkbox,
  Select,
} from 'antd';

import {
  BtnDelete,
} from 'components/common/button';

import { getKnowledges } from 'services/knowledgeUnit';
import { getListSubject } from 'services/Subject';
import { getListSubjectType } from 'services/subjectType';
import { getCertificateTypes } from 'services/certificateType';
import { TableForm, SelectTable } from 'components/common';

@Form.create({ name: 'tableDetailProgram' })
class TableDetailProgram extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {
      knowledgesData: [],
      listSubjectData: {},
      listSubjectTypeData: [],
      certificateTypesData: [],
    }
  }

  componentWillMount = async () => {
    // load data not depend
    await Promise.all([
      getKnowledges({}),
      getListSubjectType({}),
      getCertificateTypes({}),
    ])
      .then((responses) => {
        this.setState({
          knowledgesData: [...responses[0].data.items],
          listSubjectTypeData: [...responses[1].data.items],
          certificateTypesData: [...responses[2].data.items],
        });
      })
      .catch(() => {
        message.error('Có lỗi xãy ra');
      });
    await this.getDataForDataDepend(this.props.data);
  }

  componentWillReceiveProps = async (nextProps) => {
    if (nextProps.data !== this.props.data) {
      await this.getDataForDataDepend(nextProps.data);
    }
  };

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  getDataForDataDepend = async (data) => {
    if (!data) {
      return;
    }
    const { listSubjectData: rawListSubjectData } = this.state;
    const listSubjectData = { ...rawListSubjectData };
    data.forEach(element => {
      if (!listSubjectData[element.knowledgeId]) {
        listSubjectData[element.knowledgeId] = null;
      }
    });
    // load data depend
    await Promise.all([
      ...Object.keys(listSubjectData).map(key => {
        // only load data that not have in list
        if (!listSubjectData[key]) {
          return getListSubject({ knowledgeId: key })
        }
        return null;
      })
    ])
      .then((responses) => {
        Object.keys(listSubjectData).forEach((key, index) => {
          // only set data for pknowledgeId that dont have data
          if (!listSubjectData[key]) {
            listSubjectData[key] = [...responses[index].data.items];
          }
        })
        this.setState({
          listSubjectData,
        });
      })
      .catch(() => {
        message.error('Có lỗi xãy ra');
      });
  }

  loadOneDataDepend = async (api, id, params) => {
    const { listSubjectData } = this.state;
    const tempListSubjectData = { ...listSubjectData };
    const response = await api(params);
    tempListSubjectData[id] = [...response.data.items];
    this.setState({
      listSubjectData: tempListSubjectData,
    });
  }

  columns = [
    {
      title: 'Khối kiến thức',
      dataIndex: 'knowledgeId',
      key: 'knowledgeId',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        const { listSubjectData } = this.state;
        return (
          <Form.Item>
            {getFieldDecorator(`knowledgeId-${record.id}`, { initialValue: record.knowledgeId })(
              <SelectTable
                data={this.state.knowledgesData}
                onChange={(knowledgeId) => {
                  if (!listSubjectData[knowledgeId]) {
                    this.loadOneDataDepend(getListSubject, knowledgeId, { knowledgeId })
                  }
                  this.props.form.setFieldsValue({
                    [`subjectId-${record.id}`]: null,
                  });
                }}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Học phần',
      dataIndex: 'subjectId',
      key: 'subjectId',
      render: (_text, record) => {
        const {
          getFieldDecorator,
          getFieldValue
        } = this.props.form;
        const { listSubjectData } = this.state;
        const knowledgeId = getFieldValue(`knowledgeId-${record.id}`);
        return (
          <Form.Item>
            {listSubjectData[knowledgeId] ?
              getFieldDecorator(`subjectId-${record.id}`, {
                initialValue: record.subjectId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Học phần!',
                  },
                ],
              })(
                <SelectTable
                  data={this.state.listSubjectData[knowledgeId]}
                />
              )
              :
              <Select
                style={{ width: '100%' }}
                loading
              />
            }
          </Form.Item>
        );
      },
    },
    {
      title: 'Loại môn học',
      dataIndex: 'subjectTypeId',
      key: 'subjectTypeId',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`subjectTypeId-${record.id}`, {
              initialValue: record.subjectTypeId,
            })(
              <SelectTable
                data={this.state.listSubjectTypeData}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Kỳ thứ',
      dataIndex: 'semesterNo',
      key: 'semesterNo',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`semesterNo-${record.id}`, {
              initialValue: record.semesterNo,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Kỳ thứ!',
                },
              ],
            })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Số tín chỉ',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`noOfCredit-${record.id}`, {
              initialValue: record.noOfCredit,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Số tín chỉ!',
                },
              ],
            })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Hệ số',
      dataIndex: 'multiplyBy',
      key: 'multiplyBy',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`multiplyBy-${record.id}`, { initialValue: record.multiplyBy })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Lý thuyết',
      dataIndex: 'theoryPeriod',
      key: 'theoryPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`theoryPeriod-${record.id}`, {
              initialValue: record.theoryPeriod,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Lý thuyết!',
                },
              ],
            })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Thực hành',
      dataIndex: 'practicePeriod',
      key: 'practicePeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`practicePeriod-${record.id}`, {
              initialValue: record.practicePeriod,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Thực hành!',
                },
              ],
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Bài tập',
      dataIndex: 'exercisePeriod',
      key: 'exercisePeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`exercisePeriod-${record.id}`, {
              initialValue: record.exercisePeriod,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Bài tập!',
                },
              ],
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Thảo luận',
      dataIndex: 'groupPeriod',
      key: 'groupPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`groupPeriod-${record.id}`, {
              initialValue: record.groupPeriod,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Thảo luận!',
                },
              ],
            })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Tự học',
      dataIndex: 'selfPeriod',
      key: 'selfPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`selfPeriod-${record.id}`, {
              initialValue: record.selfPeriod,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Tự học!',
                },
              ],
            })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Tổng số tiết',
      dataIndex: 'totalPeriod',
      key: 'totalPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`totalPeriod-${record.id}`, { initialValue: record.totalPeriod })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Nhóm tự chọn',
      dataIndex: 'selfChosenGroup',
      key: 'selfChosenGroup',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`selfChosenGroup-${record.id}`, {
              initialValue: record.selfChosenGroup,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Nhóm tự chọn!',
                },
              ],
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Tự chọn',
      dataIndex: 'isSelfChoose',
      key: 'isSelfChoose',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isSelfChoose-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isSelfChoose,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Tự chọn!',
                },
              ],
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Chứng chỉ',
      dataIndex: 'certificateTypeId',
      key: 'certificateTypeId',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`certificateTypeId-${record.id}`, {
              initialValue: record.certificateTypeId,
            })(
              <SelectTable
                data={this.state.certificateTypesData}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Không tính TBCHT',
      dataIndex: 'notIncludedInGrade',
      key: 'notIncludedInGrade',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`notIncludedInGrade-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.notIncludedInGrade,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Không tính TBCHT!',
                },
              ],
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Số tín chỉ tiên quyết',
      dataIndex: 'requiredCredit',
      key: 'requiredCredit',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`requiredCredit-${record.id}`, {
              initialValue: record.requiredCredit,
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Môn ĐKKL',
      dataIndex: 'isForProject',
      key: 'isForProject',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isForProject-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isForProject,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Môn ĐKKL!',
                },
              ],
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Môn TN',
      dataIndex: 'isForGraduate',
      key: 'isForGraduate',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isForGraduate-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isForGraduate,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Môn TN!',
                },
              ],
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Môn ĐKTN',
      dataIndex: 'isGraduatedCondition',
      key: 'isGraduatedCondition',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isGraduatedCondition-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isGraduatedCondition,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Môn ĐKTN!',
                },
              ],
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Thao tác',
      key: 'dcn',
      render: (_text, record) => (
        <Form.Item>
          <Popconfirm
            title="Bạn có muốn xóa?"
            okText="Xóa"
            cancelText="Hủy"
            onConfirm={() => this.props.handleDelete(record.id)}
          >
            <BtnDelete />
          </Popconfirm>
        </Form.Item>
      ),
    },
  ];

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
      filterParams
    } = this.props;
    return (
      <TableForm
        reload={reload}
        data={data}
        handleSelectedRows={handleSelectedRows}
        rowKey={rowKey}
        columns={this.columns}
        form={form}
        filterParams={filterParams}
        ref={this.refTable}
      />
    );
  }
}

export default TableDetailProgram;



