import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { BtnSearch, BtnClear, BtnAddNew } from 'components/common/button';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};

  onFilterReady = () => {
    const { form } = this.props;
    Object.keys(form.getFieldsValue()).forEach(key => {
      const obj = {};
      obj[key] = getQueryParams()[key] || null;
      form.setFieldsValue(obj);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  addNew = () => {
    this.props.addNew();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        updateQueryParams(values);
      }
    });
  };

  render() {
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
    };
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            classes
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            onReadyState={this.onFilterReady}
          />
        </Row>
        <Row>
          <Col span={6} style={{ textAlign: 'left' }}>
            <div className="btn-group">
              <BtnAddNew onClick={this.addNew} />
            </div>
          </Col>
          <Col span={18} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



