import React, { Component, Fragment } from 'react';
import { 
  Row, 
  Col, 
  Form, 
  Modal,
  Divider,
  Spin,
} from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { 
  SelectAjax, 
} from 'components/common';
import { duplicateToNewCourse } from 'services/programDefinition';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
// import { setFieldsValueCustom } from '../../../utils/formUtil';

@Form.create({ name: 'ModalCopyProgram' })
class ModalCopyProgram extends Component {
  state = {
    data: this.props.data,
    loading: true,
  };

  componentWillReceiveProps(nextProps) {
    if(this.props.data !== nextProps.data) {
      this.setState({
        data: nextProps.data,
      },
        () => this.onFilterReady(),
      );
    }
    if(nextProps.loading !== this.state.loading) { 
      this.setState({
        loading: nextProps.loading,
      });
    }
  }

  onFilterReady = () => {
    const { form } = this.props;
    const { data } = this.state;
    if(data) {
      form.setFieldsValue({
        'trainTypeId-old' : data.trainTypeId,
        'facultyId-old' : data.facultyId,
        'courseId-old' : data.courseId,
        'studyFieldId-old' : data.studyFieldId,
        trainTypeId : data.trainTypeId,
        facultyId : data.facultyId,
        courseId : data.courseId,
        studyFieldId : data.studyFieldId,
      },
      () => this.setState({loading: false}),
      );
    }
  }

  handleSave = () => {
    const { data } = this.props;
    this.props.form.validateFields(async(err, values) => {
      try {
        this.setState({
          loading: true,
        });
        await duplicateToNewCourse({
          programId: data.id,
          trainTypeId : values.trainTypeId,
          facultyId : values.facultyId,
          courseId : values.courseId,
          studyFieldId : values.studyFieldId,
        });
        this.props.onSuccess();
      } catch (error) {
        this.setState({
          loading: false,
        });
      }
    });
  }

  render() {
    const { loading } = this.state;
    const { getFieldDecorator } = this.props.form;
    const layout = {
      labelCol: { span: 8 },
      wrapperCol: { span: 16 },
    };
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
    };
    return (
      <Modal
        title='Sao chép chương trình đào tạo'
        visible={this.props.visible}
        onOk={this.handleSave}
        okText="Lưu"
        onCancel={this.props.onCancel}
        cancelText="Hủy"
        width={550}
      >
        <Spin spinning={loading}>
          <Form {...layout} onSubmit={this.handleSave}>
            <Fragment>
              <Divider orientation="left">Chương trình đào tạo cũ</Divider>
              <Row gutter={24}>
                <Col span={20} offset={2}> 
                  <Form.Item {...formItemLayout} label="Hệ">
                    {getFieldDecorator('trainTypeId-old')(
                      <SelectAjax 
                        service={getTrainTypes} 
                        disabled
                      />
                    )}
                  </Form.Item>
                  <Form.Item {...formItemLayout} label="Khoa">
                    {getFieldDecorator('facultyId-old')(
                      <SelectAjax
                        service={getFacultys}
                        disabled
                      />
                    )}
                  </Form.Item>
                  <Form.Item {...formItemLayout} label="Khoá">
                    {getFieldDecorator('courseId-old')(
                      <SelectAjax
                        service={getCourses}
                        disabled
                      />
                    )}
                  </Form.Item>
                  <Form.Item {...formItemLayout} label="CN">
                    {getFieldDecorator('studyFieldId-old')(
                      <SelectAjax
                        service={getStudyFields}
                        disabled
                      />
                    )}
                  </Form.Item>
                </Col>
              </Row>
              <Divider />
              <Divider orientation="left">Chương trình đào tạo mới</Divider>
              <Row gutter={24}>
                <Col span={20} offset={2}> 
                  <FilterPathEducate 
                    classes 
                    column
                    form={this.props.form} 
                    optionFieldDecorator={optionFieldDecorator} 
                    onReadyState={this.onFilterReady} 
                  />
                </Col>
              </Row>
            </Fragment>
          </Form>
        </Spin>
        
      </Modal>
    );
  }
}

export default ModalCopyProgram;



