import React, { PureComponent, Fragment } from 'react';
import {
  Card,
  Row,
  Col,
  Tooltip,
  Popconfirm,
  Spin
} from 'antd';
import Page from 'components/LayoutComponents/Page';
import {
  BtnDelete,
  BtnEdit,
  Btn,
  BtnExport,
} from 'components/common/button';
import {
  getProgramDefinition,
  getProgramDefinitionById,
  deleteProgramDefinition,
  exportProgramById,
} from 'services/programDefinition';
import { TableData } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import FormSearch from './FormSearch';
import FormDetail from './FormDetail';
import ModalCopyProgram from './modalCopyProgram';
import ModalCTDT from './modalCTDT';
import ModalTCTC from './modalTCTC';
import ModalSubjectRelationship from './modalSubjectRelationship';

const ADD_NEW_TEXT = 'Thêm Mới';
const EDIT_TEXT = 'Chỉnh Sửa';

class Program extends PureComponent {
  state = {
    reload: 0,
    titleDetail: ADD_NEW_TEXT,
    paramSearch: {},
    enableDetail: false,
    dataDetail: null,
    detailLoading: false,
    copyLoading: false,
    ctdtLoading: false,
    tctcLoading: false,
    subjectLoading: false,
    pageLoading: false,
    showCopyModal: false,
    showCTDTModal: false,
    showTCTCModal: false,
    showSubjectModal: false,
    paramsCTDTModal: null,
    paramsTCTCModal: null,
    paramsSubjectModal: null,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleEdit = async (id) => {
    this.setState({
      enableDetail: true,
      detailLoading: true,
      dataDetail: null,
      titleDetail: EDIT_TEXT,
    });
    try {
      const res = await getProgramDefinitionById({ id });
      this.setState({
        dataDetail: res.data,
        detailLoading: false,
      });
    } catch (error) {
      this.setState({
        enableDetail: false,
        detailLoading: false,
      });
    }
  };

  handleExport = async (id) => {
    try {
      this.setState({
        pageLoading: true,
      });
      await exportProgramById({ id });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        pageLoading: false,
      });
    }
  };

  handleDelete = async (id) => {
    try {
      await deleteProgramDefinition(id);
      this.setState({
        reload: this.state.reload + 1,
      });
    } catch (error) {
      console.log('error delete ProgramDefinition: ', error);
    }
  }

  handleShowCopyModal = async (id) => {
    const { dataDetail } = this.state;
    this.setState({
      showCopyModal: true,
      copyLoading: true,
    });
    if (!dataDetail || dataDetail.id !== id) {
      const res = await getProgramDefinitionById({ id });
      this.setState({
        dataDetail: res.data,
      },
        () => this.setState({ copyLoading: false }),
      );
    } else {
      this.setState({
        copyLoading: false,
      });
    }
  }

  handleShowModalCTDT = async (record) => {
    this.setState({
      showCTDTModal: true,
      paramsCTDTModal: {
        programId: record.id,
        studyFieldId: record.studyFieldId,
      },
    });
  }

  handleShowModalTCTC = async (record) => {
    this.setState({
      showTCTCModal: true,
      paramsTCTCModal: {
        programId: record.id,
      },
    });
  }

  handleShowModalSubject = async (record) => {
    this.setState({
      showSubjectModal: true,
      paramsSubjectModal: {
        programId: record.id,
      },
    });
  }

  handleOKCopyModal = () => {
    this.setState({
      reload: this.state.reload + 1,
      showCopyModal: false,
    });
  }

  handleCancelCopyModal = () => {
    this.setState({
      showCopyModal: false,
    });
  }

  handleOKCTDTModal = () => {
    this.setState({
      reload: this.state.reload + 1,
      showCTDTModal: false,
    });
  }

  handleCancelCTDTModal = () => {
    this.setState({
      showCTDTModal: false,
    });
  }

  handleOKTCTCModal = () => {
    this.setState({
      reload: this.state.reload + 1,
      showTCTCModal: false,
    });
  }

  handleCancelTCTCModal = () => {
    this.setState({
      showTCTCModal: false,
    });
  }

  handleOKSubjectModal = () => {
    this.setState({
      reload: this.state.reload + 1,
      showSubjectModal: false,
    });
  }

  handleCancelSubjectModal = () => {
    this.setState({
      showSubjectModal: false,
    });
  }

  addNew = () => {
    this.setState({
      enableDetail: true,
      dataDetail: null,
      titleDetail: ADD_NEW_TEXT,
      detailLoading: true,
    },
      () => this.setState({
        detailLoading: false,
      }),
    );
  }

  handleDetailSuccess = () => {
    this.setState({
      enableDetail: false,
      dataDetail: null,
      titleDetail: null,
      detailLoading: false,
      reload: this.state.reload + 1,
    });
  }

  handleDetailCancel = () => {
    this.setState({
      enableDetail: false,
      dataDetail: null,
      titleDetail: null,
      detailLoading: false,
    });
  }

  columnsChuyen = [
    {
      title: 'CTĐT',
      dataIndex: 'programNo',
      key: 'programNo',
    },
    {
      title: 'Hệ',
      dataIndex: 'trainTypeLabel',
      key: 'trainTypeLabel',
    },
    {
      title: 'Khoa',
      dataIndex: 'facultyLabel',
      key: 'facultyLabel',
    },
    {
      title: 'Khóa',
      dataIndex: 'courseLabel',
      key: 'courseLabel',
    },
    {
      title: 'Chuyên ngành',
      dataIndex: 'studyFieldLabel',
      key: 'studyFieldLabel',
    },
    {
      title: 'SL tín chỉ',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'SL học kỳ',
      dataIndex: 'noOfSemester',
      key: 'noOfSemester',
    },
    {
      title: 'Thao tác',
      key: 'dcn',
      render: (_text, record) => (
        <Fragment>
          <Tooltip title="Quan hệ môn học">
            <Btn icon="deployment-unit" onClick={() => this.handleShowModalSubject(record)} />
          </Tooltip>
          <Tooltip title="Tín chỉ tự chọn">
            <Btn icon="file-protect" onClick={() => this.handleShowModalTCTC(record)} />
          </Tooltip>
          <Tooltip title="Gán CTĐT">
            <Btn icon="pull-request" onClick={() => this.handleShowModalCTDT(record)} />
          </Tooltip>
          <Tooltip title="Sao chép">
            <Btn icon="copy" onClick={() => this.handleShowCopyModal(record.id)} />
          </Tooltip>
          <Tooltip title="Chỉnh sửa">
            <BtnEdit onClick={() => this.handleEdit(record.id)} />
          </Tooltip>
          <Tooltip title="Xuất">
            <BtnExport title="" onClick={() => this.handleExport(record.id)} />
          </Tooltip>
          <Tooltip title="Xóa">
            <Popconfirm
              title="Bạn chắc chắn muốn xóa?"
              onConfirm={() => this.handleDelete(record.id)}
              okText="Xóa"
              cancelText="Hủy"
            >
              <BtnDelete />
            </Popconfirm>
          </Tooltip>
        </Fragment>
      ),
    },
  ];

  render() {
    const {
      paramSearch,
      enableDetail,
      dataDetail,
      detailLoading,
      titleDetail,
      copyLoading,
      showCTDTModal,
      paramsCTDTModal,
      ctdtLoading,
      showTCTCModal,
      paramsTCTCModal,
      tctcLoading,
      showSubjectModal,
      paramsSubjectModal,
      subjectLoading,
      pageLoading
    } = this.state;
    return (
      <Spin spinning={pageLoading}>
        <div>
          <Card title="Chương trình đạo tạo">
            <Row>
              <Col span={24}>
                <FormSearch addNew={this.addNew} />
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                {/* <Table bordered dataSource={datachuyennganh} columns={columnsChuyen} /> */}
                <TableData
                  reload={this.state.reload}
                  columns={this.columnsChuyen}
                  service={getProgramDefinition}
                  paramSearch={paramSearch}
                  allowGetData={
                    !!paramSearch.trainTypeId &&
                    !!paramSearch.facultyId &&
                    !!paramSearch.courseId &&
                    !!paramSearch.studyFieldId
                  }
                  rowKey="id"
                />
              </Col>
            </Row>
            {enableDetail &&
              <FormDetail
                title={titleDetail}
                loading={detailLoading}
                data={dataDetail}
                onSuccess={this.handleDetailSuccess}
                onCancel={this.handleDetailCancel}
              />
            }
          </Card>
          <ModalCopyProgram
            visible={this.state.showCopyModal}
            loading={copyLoading}
            data={this.state.dataDetail}
            onSuccess={this.handleOKCopyModal}
            onCancel={this.handleCancelCopyModal}
          />
          <ModalCTDT
            visible={showCTDTModal}
            loading={ctdtLoading}
            paramsSearch={paramsCTDTModal}
            onSuccess={this.handleOKCTDTModal}
            onCancel={this.handleCancelCTDTModal}
          />
          <ModalTCTC
            visible={showTCTCModal}
            loading={tctcLoading}
            paramsSearch={paramsTCTCModal}
            onSuccess={this.handleOKTCTCModal}
            onCancel={this.handleCancelTCTCModal}
          />
          <ModalSubjectRelationship
            visible={showSubjectModal}
            loading={subjectLoading}
            paramsSearch={paramsSubjectModal}
            onSuccess={this.handleOKSubjectModal}
            onCancel={this.handleCancelSubjectModal}
          />
        </div>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <Program />
  </Page>
);



