import React, { Component, Fragment } from 'react';
import { 
  Row, 
  Form, 
  Modal,
  Spin,
  Checkbox
} from 'antd';
import { 
  TableData, 
} from 'components/common';
import { 
  getProgramByClass, 
  createAssignProgramToClass 
} from 'services/programDefinition';

@Form.create({ name: 'ModalCTDT' })
class ModalCTDT extends Component {
  state = {
    paramsSearch: {},
    loading: false,
    reload: 0,
    rowKey: 'tempUniqueId',
  };

  componentWillReceiveProps(nextProps) {
    if(this.props.paramsSearch !== nextProps.paramsSearch) {
      this.setState({
        paramsSearch: {...nextProps.paramsSearch},
      },
      () => this.setState({
        reload: this.state.reload + 1,
      }),
      );
    }
    if(nextProps.loading !== this.state.loading) { 
      this.setState({
        loading: nextProps.loading,
      });
    }
  }

  handleSave = () => {
    this.props.form.validateFields(async(_errors, values) => {
      const { paramsSearch } = this.props;
      try {
        this.setState({
          loading: true,
        });
        const tempClassIds = [];
        Object.keys(values).forEach((key) => {
          const id = key.split('-')[2];
          if(values[key]) {
            tempClassIds.push(id);
          }
        });
        await createAssignProgramToClass({
          programId: paramsSearch.programId,
          classIds: [...tempClassIds],
        });
        this.props.onSuccess();
      } catch (error) {
        this.setState({
          loading: false,
        });
      }
    });
  }

  handleCancel = () => {
    this.props.onCancel();
  }

  columnsCTDT = [
    {
      title: '',
      dataIndex: 'isChecked',
      key: 'isChecked',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item valuePropName='checked'>
            {getFieldDecorator(`classId-${record.tempUniqueId}-${record.classId}`, {initialValue: record.isChecked, valuePropName: 'checked'})(
              <Checkbox />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'Hệ',
      dataIndex: 'trainType',
      key: 'trainType',
    },
    {
      title: 'Khóa',
      dataIndex: 'course',
      key: 'course',
    },

  ];

  render() {
    const { 
      paramsSearch, 
      loading,
      rowKey,
    } = this.state;
    return (
      <Modal
        title='Gán chương trình đào tạo'
        visible={this.props.visible}
        onOk={this.handleSave}
        okText="Lưu"
        onCancel={this.handleCancel}
        cancelText="Hủy"
        width={800}
      >
        <Spin spinning={loading}>
          <Form layout="vertical" onSubmit={this.handleSave}>
            <Fragment>
              <Row gutter={24}>
                <TableData
                  reload={this.state.reload}
                  columns={this.columnsCTDT}
                  service={getProgramByClass}
                  paramSearch={paramsSearch}
                  allowGetData={!!paramsSearch.programId && !!paramsSearch.studyFieldId}
                  rowKey={rowKey}
                />
              </Row>
            </Fragment>
          </Form>
        </Spin>
        
      </Modal>
    );
  }
}

export default ModalCTDT;



