import { request } from 'utils';

const prefix = '/api/categories';

export const getSubjectTypes = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: `/SubjectType`,
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
  });

export const getListSubjectType = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/SubjectType`,
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
  });

export const addSubjectGroup = dataAddNew =>
  request({
    prefix,
    url: `/SubjectGroup`,
    data: dataAddNew,
    method: 'POST',
  });

export const getSubjectGroupById = idSubject =>
  request({
    prefix,
    url: `/SubjectGroup/GetById`,
    params: { id: idSubject },
    method: 'GET',
  });

export const editSubjectGroup = subjectGroup =>
  request({
    prefix,
    url: `/SubjectGroup`,
    data: subjectGroup,
    method: 'PUT',
  });

export const deleteSubjectGroup = idSubject =>
  request({
    prefix,
    url: `/SubjectGroup`,
    data: {
      id: idSubject,
    },
    method: 'DELETE',
  });



