import React, { Component, Fragment } from 'react';
import { 
  Row, 
  Col,
  Form, 
  Modal,
  Spin,
  Popconfirm,
} from 'antd';
import { 
  getSubjectRelationship,
  getSubjectByProgram,
  getSubjectRelationshipTypes,
  deleteSubjectRelationship,
  saveSubjectRelationship
} from 'services/subjectRelationship';
import { 
  BtnAddNew, 
  BtnDelete,
  BtnSave,
  BtnCancel,
} from 'components/common/button';
import { 
  SelectAjax,
  TableData 
} from 'components/common';

@Form.create({ name: 'ModalSubjectRelationship' })
class ModalSubjectRelationship extends Component {
  state = {
    paramsSearch: {},
    loading: false,
    reload: 0,
    isAddNew: false,
  };

  componentWillReceiveProps(nextProps) {
    if(this.props.paramsSearch !== nextProps.paramsSearch) {
      this.setState({
        paramsSearch: {...nextProps.paramsSearch},
      },
      () => this.setState({
        reload: this.state.reload + 1,
      }),
      );
    }
    if(nextProps.loading !== this.state.loading) { 
      this.setState({
        loading: nextProps.loading,
      });
    }
  }

  handleSave = async () => {
    this.props.form.validateFields(async(errors, values) => {
      if(errors) {
        return;
      }
      try {
        const { paramsSearch } = this.state;
        this.setState({
          loading: true,
        });
        await saveSubjectRelationship({
          firstSubjectId: values.firstSubjectId, 
          secondSubjectId: values.secondSubjectId,
          programId: paramsSearch.programId,
          relationTypeId: values.relationTypeId, 
        });
        this.setState({
          isAddNew: false,
        });
      } catch (error) {
        console.log(error);
      } finally {
        this.setState({
          loading: false,
        });
      }
    });
  }

  handleCancelAddNew = () => {
    this.setState({
      isAddNew: false,
    });
  }

  handleCancelModal = () => {
    this.props.onCancel();
  }

  handleDelete = async record => {
    const { reload, paramsSearch } = this.state;
    try {
      this.setState({
        loading: true,
      });
      await deleteSubjectRelationship({
        firstSubjectId: record.firstSubjectId, 
        secondSubjectId: record.secondSubjectId,
        programId: paramsSearch.programId,
      });
      this.setState({
        reload: reload + 1,
      });
    } catch (err) {
      console.log(err);  
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  addNew = () => {
    this.setState({
      isAddNew: true,
    });
  };

  columnsSubject = [
    {
      title: 'Học phần 1',
      dataIndex: 'firstSubjectLabel',
      key: 'firstSubjectLabel',
    },
    {
      title: 'Học phần 2',
      dataIndex: 'secondSubjectLabel',
      key: 'secondSubjectLabel',
    },
    {
      title: 'Loại quan hệ',
      dataIndex: 'relationTypeLabel',
      key: 'relationTypeLabel',
    },
    {
      title: 'Thao tác',
      key: 'dcn',
      render: (_text, record) => (
        <Fragment>
          <Form.Item>
            <Popconfirm
              title="Bạn có muốn xóa?"
              okText="Xóa"
              cancelText="Hủy"
              onConfirm={() => this.handleDelete(record)}
            >
              <BtnDelete />
            </Popconfirm>
          </Form.Item>
        </Fragment>
      ),
    },
  ];

  render() {
    const { 
      loading,
      reload,
      paramsSearch,
      isAddNew,
    } = this.state;
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Modal
        title={isAddNew ? 'Thêm quan hệ môn học' : 'Quan hệ môn học'}
        visible={this.props.visible}
        onCancel={this.handleCancelModal}
        width={800}
        footer={null}
      >
        <Spin spinning={loading}>
          <Form {...formItemLayout} onSubmit={this.handleSave}>
            { isAddNew ? 
              <Fragment>
                <Form.Item label="Học phần 1">
                  {getFieldDecorator(`firstSubjectId`, {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng chọn Học phần 1!',
                      },
                    ],
                  })(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getSubjectByProgram}
                      params={{ programId: paramsSearch.programId }}
                      allowGetData={!!paramsSearch.programId}
                    />
                  )}
                </Form.Item>
                <Form.Item label="Học phần 2">
                  {getFieldDecorator(`secondSubjectId`, {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng chọn Học phần 2!',
                      },
                    ],
                  })(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getSubjectByProgram}
                      params={{ programId: paramsSearch.programId }}
                      allowGetData={!!paramsSearch.programId}
                    />
                  )}
                </Form.Item>
                <Form.Item label="Loại quan hệ">
                  {getFieldDecorator(`relationTypeId`, {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng chọn Loại quan hệ!',
                      },
                    ],
                  })(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getSubjectRelationshipTypes}
                    />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label=" ">
                  <BtnSave onClick={this.handleSave} />
                  <BtnCancel onClick={this.handleCancelAddNew} />
                </Form.Item>
              </Fragment>
              :
              <Fragment>
                <Row gutter={24}>
                  <Col span={6} style={{ textAlign: 'left' }}>
                    <div className="btn-group">
                      <BtnAddNew onClick={this.addNew} />
                    </div>
                  </Col>
                </Row>
                <Row gutter={24}>
                  <TableData
                    reload={reload}
                    columns={this.columnsSubject}
                    service={getSubjectRelationship}
                    paramSearch={paramsSearch}
                    allowGetData={!!paramsSearch.programId}
                  />
                </Row>
              </Fragment>
            }
          </Form>
        </Spin>
        
      </Modal>
    );
  }
}

export default ModalSubjectRelationship;



