import { request } from 'utils';

const prefix = '/api/education';

export const getSubjectRelationship= (
  { 
    programId,
    relationshipId,
    page, 
    pageSize = 10, 
    orderBy, 
    isPagingEnabled = true,
  }
) =>
  request({
    prefix,
    url: `/SubjectRelationship`,
    method: 'GET',
    params: {
      programId,
      relationshipId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getSubjectRelationshipTypes= (
    { 
      page, 
      pageSize = 10, 
      orderBy, 
      isPagingEnabled = true,
    }
  ) =>
    request({
      prefix,
      url: `/SubjectRelationship/RelationshipTypes`,
      method: 'GET',
      params: {
        page,
        pageSize,
        orderBy,
        isPagingEnabled,
      },
      data: {},
    });

export const getSubjectByProgram= (
    { 
      programId,
      page, 
      pageSize = 10, 
      orderBy, 
      isPagingEnabled = true,
    }
  ) =>
    request({
      prefix,
      url: `/SubjectRelationship/SubjectByProgram`,
      method: 'GET',
      params: {
        programId,
        page,
        pageSize,
        orderBy,
        isPagingEnabled,
      },
      data: {},
    });

export const deleteSubjectRelationship = (
  { 
    firstSubjectId, 
    secondSubjectId, 
    programId, 
  }
) =>
  request({
    prefix,
    url: `/SubjectRelationship`,
    method: 'DELETE',
    params: {
      firstSubjectId, 
      secondSubjectId, 
      programId, 
    }
  });

export const saveSubjectRelationship = ({ 
  firstSubjectId, 
  secondSubjectId,
  programId,
  relationTypeId 
}) =>
  request({
    prefix,
    url: `/SubjectRelationship`,
    data: {
      firstSubjectId, 
      secondSubjectId,
      programId,
      relationTypeId 
    },
    method: 'POST',
  });




