import React, { PureComponent, Fragment } from 'react';
import {
  Row,
  Col,
  Input,
  Form,
  Button,
  Icon,
  Upload,
  Spin,
  Divider,
  Collapse,
  message,
} from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import {
  SelectAjax,
} from 'components/common';
import {
  BtnSave,
  BtnCancel,
  BtnAddNew,
  BtnSearch,
  BtnClear,
} from 'components/common/button';
import { getKnowledges } from 'services/knowledgeUnit';
import { getListSubject } from 'services/Subject';
import { makeid } from 'utils/functionUtil';

import {
  uploadFilePrograms,
  addProgramDefinition,
  editProgramDefinition,
} from 'services/programDefinition';
import {
  setFieldsValueCustom,
  getListDataValid
} from 'utils/formUtil';

import TableDetailProgram from './tableDetailProgram';

const { Panel } = Collapse;
const ADD_NEW_TEXT = 'Thêm Mới';
const EDIT_TEXT = 'Chỉnh Sửa';

@Form.create({ name: 'FormDetail' })
class FormDetail extends PureComponent {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.refFormRaw = React.createRef();
    this.state = {
      reload: 0,
      data: null,
      tableDataToView: [],
      loading: true,
      rowKey: "id",
      filterParams: {},
    };
  }

  componentDidMount = () => {
    const { form, data } = this.props;
    if (data) {
      this.setState(
        {
          data: { ...data },
          tableDataToView: [...data.programDetails],
        },
        () => setFieldsValueCustom(form, data)
      );
    }
  };

  componentWillReceiveProps = nextProps => {
    const { data, loading } = this.state;
    const { form } = this.props;
    if (nextProps.data && nextProps.loading === false && (!data || data.id !== nextProps.data.id)) {
      this.setState(
        {
          data: { ...nextProps.data },
          tableDataToView: [...nextProps.data.programDetails],
        },
        () => setFieldsValueCustom(form, nextProps.data)
      );
    }

    if (!nextProps.data && nextProps.loading) {
      this.setState(
        {
          data: null,
          tableDataToView: [],
        },
        () => this.props.form.resetFields()
      );
    }

    if (nextProps.loading !== loading) {
      this.setState({
        loading: nextProps.loading,
      });
    }
  };

  onSubmit = () => {
    const { rowKey } = this.state;
    const { title, data } = this.props;
    this.props.form.validateFields(async err1 => {
      if (err1) {
        return;
      }
      const listFieldName = [
        'subjectId',
        'semesterNo',
        'noOfCredit',
        'theoryPeriod',
        'practicePeriod',
        'exercisePeriod',
        'groupPeriod',
        'selfPeriod',
        'isSelfChoose',
        'selfChosenGroup',
        'notIncludedInGrade',
        'isForProject',
        'isForGraduate',
        'isGraduatedCondition',
      ];

      this.setState({
        loading: true,
      });

      getListDataValid(rowKey, listFieldName, this.refForm, this.refFormRaw)
        .then(
          async (listSelectedItem) => {
            try {
              const {
                programNo,
                noOfCredit,
                noOfCreditForGraduate,
                noOfSemester,
                trainTypeId,
                facultyId,
                courseId,
                studyFieldId
              } = this.props.form.getFieldsValue([
                'programNo',
                'noOfCredit',
                'noOfCreditForGraduate',
                'noOfSemester',
                'trainTypeId',
                'facultyId',
                'courseId',
                'studyFieldId'
              ]);

              const formData = {
                programNo,
                noOfCredit,
                noOfCreditForGraduate,
                noOfSemester,
                trainTypeId,
                facultyId,
                courseId,
                studyFieldId,
                createProgramDetails: [...listSelectedItem],
              }

              if (title === ADD_NEW_TEXT) {
                this.setState({
                  loading: true,
                });
                await addProgramDefinition(formData);
              } else {
                formData.id = data.id;
                await editProgramDefinition(formData);
              }
              this.props.onSuccess();
              message.success('Lưu thành công');
              this.setState({
                data: null,
                tableDataToView: [],
              });
            } catch (error) {
              console.log(error);
            } finally {
              this.setState({
                loading: false,
              });
            }
          }
        )
        .catch(
          () => {
            this.setState({
              loading: false,
            });
          }
        )
    });
  };

  onCancel = () => {
    this.props.onCancel();
  };

  onFilterReady = () => {
    const { title, form, data } = this.props;
    if (title === EDIT_TEXT && data && data.trainTypeId) {
      form.setFieldsValue({
        trainTypeId: data.trainTypeId,
        facultyId: data.facultyId,
        courseId: data.courseId,
        studyFieldId: data.studyFieldId,
      });
    }
  };

  handleDeleteItem = async id => {
    const currentTableData = this.refFormRaw.getTableData();
    const temptableDataToView = currentTableData.filter(element => {
      return element.id !== id;
    });
    this.setState({
      tableDataToView: [...temptableDataToView],
    });
  };

  handleAddNewItem = () => {
    const currentTableData = this.refFormRaw.getTableData();
    const tempNewData = { ...this.newtableDataToView };
    tempNewData.id = makeid(8);
    currentTableData.unshift(tempNewData);
    this.setState({
      tableDataToView: [...currentTableData],
    });
  };

  handleUploadFile = async dataFile => {
    try {
      this.setState({
        loading: true,
      });
      const res = await uploadFilePrograms(dataFile.file);
      const data = [...res.data.programDetails];
      data.forEach((item) => {
        item.id = makeid(8);
      });
      this.setState({
        tableDataToView: [...data],
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleFilterProgramDetail = () => {
    const { knowledgeId, semesterNo, subjectId } = this.props.form.getFieldsValue([
      'knowledgeId',
      'semesterNo',
      'subjectId',
    ]);

    this.setState({
      filterParams: {
        knowledgeId,
        semesterNo,
        subjectId
      },
    });
  };

  handleResetFilterProgramDetail = () => {
    this.props.form.setFieldsValue({
      knowledgeId: null,
      semesterNo: null,
      subjectId: null,
    });
    this.setState({
      filterParams: {},
    });
  };

  newtableDataToView = {
    id: 0,
    programId: 0,
    subjectId: '1',
    subjectLabel: 'test',
    knowledgeId: 1,
    knowledgeLabel: 'Khối kiến thức chung',
    subjectGroupId: 'CB',
    subjectGroupLabel: 'Cơ bản',
    subjectTypeId: 2,
    subjectTypeLabel: 'string123',
    semesterNo: 1,
    noOfCredit: 2,
    multiplyBy: 1,
    theoryPeriod: 45,
    practicePeriod: 0,
    exercisePeriod: 0,
    groupPeriod: 0,
    selfPeriod: 0,
    totalPeriod: 45,
    isSelfChoose: false,
    selfChosenGroup: 0,
    certificateTypeId: 'GDTC',
    notIncludedInGrade: true,
    requiredCredit: 0,
    isForProject: false,
    isForGraduate: false,
    isGraduatedCondition: true,
  };

  render() {
    const { title } = this.props;
    const { tableDataToView, loading, rowKey, reload, filterParams } = this.state;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const knowledgeId = getFieldValue('knowledgeId');
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
    };
    return (
      <Fragment>
        <Spin spinning={loading}>
          <Divider className="title" orientation="center">
            {title}
          </Divider>
          <Form layout="vertical" className="form-inline-label">
            <Collapse defaultActiveKey={['1']}>
              <Panel header="Thông tin chung" key="1">
                <Row gutter={24}>
                  <Col span={5}>
                    <Form.Item label="Chương trình đạo tạo">
                      {getFieldDecorator('programNo', {
                        rules: [
                          {
                            required: true,
                            message: 'Vui lòng điền Chương trình đào tạo',
                          },
                        ],
                      })(<Input />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="SL tín chỉ">
                      {getFieldDecorator('noOfCredit', {
                        rules: [
                          {
                            required: true,
                            message: 'Vui lòng điền SL tín chỉ',
                          },
                        ],
                      })(<Input />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="SL tín chỉ tích lũy">
                      {getFieldDecorator('noOfCreditForGraduate', {
                        rules: [
                          {
                            required: true,
                            message: 'Vui lòng điền SL tín chỉ tích lũy',
                          },
                        ],
                      })(<Input />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="SL học kỳ">
                      {getFieldDecorator('noOfSemester', {
                        rules: [
                          {
                            required: true,
                            message: 'Vui lòng điền SL học kỳ',
                          },
                        ],
                      })(<Input />)}
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={24}>
                  <FilterPathEducate
                    classes
                    form={this.props.form}
                    optionFieldDecorator={optionFieldDecorator}
                    onReadyState={this.onFilterReady}
                  />
                </Row>
              </Panel>
              <Panel header="Bộ lọc dữ liệu chi tiết" key="2">
                <Row gutter={24}>
                  <Col span={5}>
                    <Form.Item label="Khối kiến thức">
                      {getFieldDecorator('knowledgeId')(<SelectAjax service={getKnowledges} />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="Kỳ thứ">
                      {getFieldDecorator('semesterNo')(<Input />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="Học phần">
                      {getFieldDecorator('subjectId')(
                        <SelectAjax
                          setFieldsValue={setFieldsValue}
                          service={getListSubject}
                          params={{ knowledgeId }}
                          allowGetData={!!knowledgeId}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={9} style={{ textAlign: 'left' }}>
                    <Form.Item label="actions" className="none-label">
                      {getFieldDecorator('noOfSemester')(
                        <div className="btn-group">
                          <BtnSearch onClick={this.handleFilterProgramDetail} />
                          <BtnClear onClick={this.handleResetFilterProgramDetail} />
                        </div>
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Panel>
              <Panel header="Dữ liệu chi tiết" key="3">
                <Row gutter={24}>
                  <Col span={6} style={{ textAlign: 'left' }}>
                    <div className="btn-group">
                      <BtnAddNew onClick={this.handleAddNewItem} />
                      <Upload
                        accept=".xlsx"
                        beforeUpload={() => false}
                        listType="text"
                        onChange={uploadData => this.handleUploadFile(uploadData)}
                        showUploadList={false}
                      >
                        <Button>
                          <Icon type="upload" /> Chọn file
                        </Button>
                      </Upload>
                    </div>
                  </Col>
                </Row>
                <Row gutter={24}>
                  <Col span={24}>
                    <TableDetailProgram
                      reload={reload}
                      data={tableDataToView}
                      ref={this.refForm}
                      rowKey={rowKey}
                      filterParams={filterParams}
                      wrappedComponentRef={(form) => {
                        this.refFormRaw = form;
                      }}
                      handleDelete={this.handleDeleteItem}
                    />
                  </Col>
                </Row>
                <Row gutter={24}>
                  <Col offset={10} span={14}>
                    <BtnSave onClick={this.onSubmit} />
                    <BtnCancel onClick={this.onCancel} />
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Form>
        </Spin>
      </Fragment>
    );
  }
}

export default FormDetail;



