export const exportProgramById = ({ id }) =>
  requestDownload({
    prefix,
    defaultFileName: `CTDT-${id}.pdf`,
    url: `/ProgramDefinition/ExportExcelProgramDefinition/${id}`,
    method: 'POST',
  });

export const getSubjectByProgramDetail = ({
  classId,
  yearEduId,
  semesterId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ProgramDefinition/SubjectByProgramDetail',
    method: 'GET',
    params: {
      classId,
      yearEduId,
      semesterId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });


export const getSemesterNo = (
  {
    page,
    pageSize = 10,
    orderBy,
    isPagingEnabled = true,
  }
) =>
  request({
    prefix,
    url: `/ProgramDefinition/SemesterNo`,
    method: 'GET',
    params: {
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });



